import './Card.css';

function Card({ image, title, description, url }) {
    return (
        <div className="card">
            <div className="card__left">
                <div className="card__image">
                    <img src={image} alt="" />
                </div>
            </div>
            <div className="card__right">
                <h3>{title}</h3>
                <p>{description}</p>
                <div className="card__actions">
                    <i className="fab fa-twitter"></i>
                </div>
            </div>
        </div>
    );
}

export default Card;