import axios from 'axios';
import { useEffect, useState } from 'react';
import Card from '../../components/Card/Card';
import CardLoading from '../../components/CardLoading/CardLoading';
import './Home.css';

function Home() {
    const [articles, setArticles] = useState(null);

    useEffect(() => {
        axios
            .get("https://gnews.io/api/v4/search?q=bitcoin&token=4bce93083d302e826bdf9db97580c270")
            .then((response) => {
                console.log(response.data.articles);
                setArticles(response.data.articles);
            });
    }, []);

    return (
        <div className="home__page">
            <div className="feed">
                {articles ? (
                    articles.map((article) => {
                        return (
                            <Card
                                key={article.content}
                                image={article.image}
                                title={article.title}
                                description={article.description}
                                url={article.url} />
                        )
                    })
                    ) : (
                        <>
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        <CardLoading />
                        </>
                    )
                }
            </div>
        </div>
    );
}

export default Home;