import Search from '../Search/Search';
import './Navbar.css';

function Navbar() {
    return (
        <header className="main__header">
            <nav className="main__navbar">
                <div className="main_navbar__logo">news feed</div>
                <div className="main_navbar__search">
                    <Search />
                </div>
            </nav>
        </header>
    );
}

export default Navbar;